import Msg from '../components/Main/Msg'
import MsgImg from '../components/Main/MsgImg'
import MsgOnlyImg from '../components/Main/Img'
import BpMsg from '../components/Main/BpMsg'
import DsMsg from '../components/Main/DsMsg'
// type 0 msg type 1 msgImg type 2 Img tpye 3 bp type 4 ds
export default {
  data () {
    return {
      chatlist: [{
        content: '~',
        likes: 666,
        type: 3
      }, {
        content: '~',
        likes: 688,
        type: 4
      }, {
        content: '',
        likes: 5,
        type: 0
      }, {
        content: '',
        likes: 15,
        type: 1
      }, {
        content: '',
        likes: 666,
        type: 2
      }, {
        content: '',
        likes: 666,
        type: 0
      }],
      bpModelVisible: false
    }
  },
  mounted () {
  },
  methods: {
    like (index) {
      this.chatlist[index].likes++
    },
    openDialog () {
      this.$bpDialog({
        title: '',
        content: '<b></b>',
        stopAuto: false,
        confirmCallback: () => {
          setTimeout(() => {
            this.$bpDialog.hide()
          }, 3000)
        }
      })
    }
  },
  components: {
    Msg,
    MsgImg,
    MsgOnlyImg,
    BpMsg,
    DsMsg
  }
}



